<template>
    <div class="djzl_bg" style="padding: 10px">
        <TopButtons/>
        <div style="height: 10px"></div>
        <a-spin :spinning="spinning" style="min-height: 500px">
            <div class="docs-content" style="margin-left:40%;padding-top: 10px !important;">
                <Content :categories="categories"/>
            </div>
        </a-spin>
    </div>
</template>

<script>
    import TopButtons from './components/TopButtons'
    import Content from './components/Content'
    import '@/assets/pc/css/timeline.css'
    import {getCategoryChildrenAndPublishedArticleList} from '@/api/category'

    export default {
        components: {
            TopButtons, Content,
        },
        data() {
            return {
                spinning: false,
                id: 6,
                categories: [],
            }
        },
        methods: {
            init() {
                this.spinning = true;
                getCategoryChildrenAndPublishedArticleList(this.id)
                    .then(res => {
                        this.spinning = false;
                        console.log(res);
                        this.categories = res.data.children;
                    })
            },
        },
        mounted() {
            this.init();
        }
    };
</script>

<style scoped>
    .djzl_bg {
        background: url(../../../assets/pc/images/djzl_bg.png) no-repeat;
    }
</style>
